@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
.countener{
  text-align: center;
  top:75%;
  left:50%;
  position: absolute;
  transform: translate(-50%, -50%);
}
.countener span{
  display: block;
}
.text1{
  font-size: 20px;
  font-weight: 700;

  margin-bottom: -20px;
  background: white;
  position: relative;
  -webkit-animation: text1 4s ease;
          animation: text1 4s ease;
  color:white;
  opacity: 0;
  font-family: Poiret One;
  white-space:nowrap;
}

.text2{
    
    -webkit-animation: text2 4s 1;
    
            animation: text2 4s 1;
    opacity: 1;
    
}
@-webkit-keyframes text1{

  0%{
    color:black;
    margin-bottom: -8px;
    opacity: 1;
    
  }
  30%{
    color:black;
    
  }
  85%{
    color:white;
    opacity: 0;
  }
}
@keyframes text1{

  0%{
    color:black;
    margin-bottom: -8px;
    opacity: 1;
    
  }
  30%{
    color:black;
    
  }
  85%{
    color:white;
    opacity: 0;
  }
}
@-webkit-keyframes text2{
  0%{
    opacity: 0;
  }
  30%{
    color:black;
    
  }
  85%{
  
    opacity: 1;
  }

}
@keyframes text2{
  0%{
    opacity: 0;
  }
  30%{
    color:black;
    
  }
  85%{
  
    opacity: 1;
  }

}
